<template>
    <div>
        <Tabs>
            <TabPane label="页面配置">
                <div class="options-bg">
                    <div class="item-input">
                        <span slot="prepend">高度:</span>
                        <InputNumber :step="10" v-model="params.height" size="small"></InputNumber>
                    </div>
                    <div class="item-input">
                        <span slot="prepend">宽度:</span>
                        <InputNumber :step="10" v-model="params.width" size="small"></InputNumber>
                    </div>
                    <div class="item-img">
                        <p>页面背景:</p>
                        <div class="bg-box" :style="{backgroundImage:params['background-image']}">
                            <div class="noop">
                                <Icon type="md-images" />
                                <p>选择图片</p>
                            </div>
                        </div>
                    </div>
                    <div class="item-sel">
                        <span>背景模式:</span>
                        <Select v-model="params['background-size']">
                            <Option
                                v-for="item in backgroundSizeList"
                                :value="item.value"
                                :key="item.value"
                            >{{ item.label }}</Option>
                        </Select>
                    </div>
                    <div class="item-picker">
                        <span>背景颜色:</span>
                        <ColorPicker v-model="params['background-color']" :hue="true" alpha />
                        <br />
                        <span>透明度：</span>
                        <Slider v-model="params.opacity" :step="0.01" :max="1" :min="0"></Slider>
                    </div>
                </div>
            </TabPane>
        </Tabs>
    </div>
</template>
<script>
export default {
    name: "options-bg",
    data() {
        return {
            backgroundSizeList: [
                {
                    value: "cover",
                    label: "覆盖"
                },
                {
                    value: "contain",
                    label: "自适应"
                },
                {
                    value: "100% auto",
                    label: "x轴拉伸"
                },
                {
                    value: "auto 100%",
                    label: "y轴拉伸"
                }
            ]
        };
    },
    props: ["params"]
};
</script>

<style lang="less" scoped>
@import url("./style.less");
</style>
